body {
  background: rgb(94, 93, 93);
  color: rgb(255, 188, 188);
  font-family: Arial, Helvetica, sans-serif;
}

li {
  list-style: none;
}

hr {
  margin: auto;
  margin-bottom: 3rem;
}

.AppContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  background: rgb(121, 119, 119);
  width: 70%;
  max-width: 500px;
  min-width: 500px;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 10px 10px 20px 5px rgb(24, 23, 23);
}

.todoList {
  margin-bottom: 2rem;
}

.todoList li {
  margin: 1rem;
}

.todoList li .title {
  color: rgb(252, 248, 248);
  display: inline-block;
  width: 300px;
  padding-right: 1rem;
  margin-left: 3rem;
  overflow-x: auto;
}

.delete {
  background: rgb(214, 13, 13);
  color: rgb(252, 248, 248);
  padding: 0.2rem;
  border-radius: 5px;
  cursor: pointer;
}

.todoInput {
  width: 70%;
  max-width: 400px;
  margin: 4rem auto;
}

.todoInput input {
  background: rgb(163, 152, 151);
  color: rgb(252, 248, 248);
  width: 74.7%;
  padding: 0.5rem;
  border-radius: 20px 0 0 20px;
  border: none;
  outline: none;
}

.todoInput button {
  background: rgb(214, 13, 13);
  color: rgb(252, 248, 248);
  width: 20%;
  padding: 0.5rem;
  border-radius: 0 20px 20px 0;
  border: none;
  cursor: pointer;
  outline: none;
}
